body {
  margin: 0;
  padding: 0;
}

header {
  width: 100%;
  height: 50px;

  position: fixed;
  top: 0;
  left: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: #213a3d;
}

header img {
  width: 220px;
  height: 32px;

  object-fit: contain;
}

main {
  width: 100%;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;

  padding: 0;
  overflow: hidden;

  background-color: white;
}

.wrap-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#btn-change-color {
  width: 100px;

  text-align: center;
  padding: 12px;
  margin-top: 10px;

  background-color: #6c757d;
  color: white;

  font-size: 20px;
  border-radius: 10px;

  cursor: pointer;
}

#btn-change-color:hover {
  background-color: #8e98a1;
}
